.ExtraDiv{
    width:30%;
    display: inline-block;
    height: 45vh;
    box-sizing: border-box;
    border:1px solid gray;
    border-radius: 5px;
    margin: 1vh;
}
.ExtraDiv h1{
    text-align: center;
    font-size:19px;
    font-family: 'Josefin Sans', sans-serif;
    color: #085076;
}
.List{
    height: 80%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.List input{
    text-align: center;
}
.List button{
    padding:5px 25px;
    width: 50%;
    box-sizing: border-box;
    display: block;
    margin:10px auto;
    font-family: 'Josefin Sans', sans-serif;
    background-color: #085076;
    color: white;
    outline: none;
    transition: 0.5s;
}
.List button:hover{
    box-shadow: 0px 0px 10px #0850769a;
}