.ConformationForm{
    width: 300px;
    height: 170px;
    box-sizing: border-box;
    padding-top: 40px;
    position: relative;
}
.ConformationForm .buttonCont{
    display:flex;
    justify-content: space-evenly;
    margin-top: 40px;
    margin-bottom: 15px;
}
.ConformationForm button{
    padding:5px 25px;
    display: block;
    margin:auto;
    font-family: 'Josefin Sans', sans-serif;
    background-color: #085076;
    color: white;
    outline: none;
}