.PaymentDetails{
    padding-bottom: 30px;
    text-align:left;
}
.PaymentDetails h1{
    font-size:19px;
    font-family: 'Josefin Sans', sans-serif;
    color: #085076;
}
.Info{
    display: flex;
    width: 100%;
    height: 100px;
    align-items: center;
}
.Info .InfoList{
    margin-left: 30px;
    color: grey;
}
.Red{
    color: red;
}
.Today li{
    list-style: none;
}
.Today h1{
    text-align: center;
    margin: 50px 0;
}
.Today .SubHead, .PrevSub{
    display: flex;
    justify-content: space-around;
    padding: 0;
}
.Today .SubHead li, .PrevSub li{
    list-style: none;
    font-family: 'Roboto', sans-serif;
    color: #707070;
    font-weight: 700;
}
.Today .SubHead li:nth-child(1){
    width: 40%;
    text-align: start;
}
.Today .SubHead li:nth-child(2),.Today .SubHead li:nth-child(3),.Today .SubHead li:nth-child(4){
    width: 20%;
    text-align: center;
}
.PrevSub li:nth-child(1),.PrevSub li:nth-child(2){
    width: 40%;
    text-align: center;
}
.PrevSub li:nth-child(3){
    width: 20%;
    text-align: center;
}
.Today .SubHead li:nth-child(1){
    width: 40%;
    text-align: start;
}
.Today .SubHead li:nth-child(2),.Today .SubHead li:nth-child(3),.Today .SubHead li:nth-child(4){
    width: 20%;
    text-align: center;
}
.Today .AllOrders{
    width: 100%;
}
.Today .AllOrders{
    padding:0;
}
.Today .Order{
    padding: 0;
    font-family: 'Roboto', sans-serif;
    display: flex;
    justify-content: space-around;
}
.Today .Order li:nth-child(1){
    width: 40%;
    text-align: start;
    font-size: 13px;
}
.Today .Order li:nth-child(2),.Today .Order li:nth-child(3),.Today .Order li:nth-child(4){
    width: 20%;
    text-align: center;
}
.Today .Order li{
    padding: 5px 0;
    border-bottom: 1px solid rgba(128, 128, 128, 0.219);
}
.TotalToday{
    text-align: right;
    padding: 0 40px;
    font-size: 20px;
    font-weight: 500;
}
.PayList{
    padding:0;
}
.PayList li{
    list-style: none;
}
.Pay{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding:0;
}
.Pay li{
    list-style: none;
    text-align: center;
    padding: 5px 0;
    border-bottom: 1px solid rgba(128, 128, 128, 0.219);
}
.Pay li:nth-child(1),.Pay li:nth-child(2){
    width: 40%;
}
.Pay li:nth-child(3){
    width: 20%;
}
.NewPayment{
    margin:50px 0;
}
.NewPayment h1{
    text-align: center;
}
.NewPay{
    margin:50px 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.NewPay button {
    font-family: 'Josefin Sans', sans-serif;
    border:1px solid #085076;
    color:white;
    background-color: #085076;
    min-width: 20%;
    display:block;
    transition: 0.5s;
    height: 30px;
    outline:none;
}
.NewPay button:hover{
    box-shadow: 0px 0px 10px #0850769a;
}
